body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.todo-list {
  background-color: white;
  position: relative;
  margin: auto;
  width: 50%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  border: 1px solid #efefef;
  box-shadow: 
    0 1px 1px rgba(0, 0, 0, 0.15),
    0 10px 0 -5px #eee,
    0 10px 1px -4px rgba(0, 0, 0, 0.15),
    0 20px 0 -10px #eee,
    0 20px 1px -9px rgba(0, 0, 0, 0.15);
  padding: 30px;
}

.todo-item {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  /* align-items: center; */
  padding: 30px 20px 0;
  width: 70%;
  border-bottom: 1px solid #cecece;
  font-family: Roboto, sans-serif;
  font-weight: 100;
  -webkit-animation: new-todo 1s 1;
          animation: new-todo 1s 1;
}

.delete-todo {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
  opacity: 0;
  transition-duration: 1s;
  transition-timing-function: ease-out;
  /* animation: new-todo 1s;
  animation-iteration-count: infinite; */
}

.add-todo {
  -webkit-transform:  translate(100% , 0, 0);
          transform:  translate(100% , 0, 0);
  transition-duration: 1s;
  transition-timing-function: linear;
  /* animation: new-todo 1s;
  animation-iteration-count: 1; */
}

@-webkit-keyframes new-todo {
  0% {
    opacity: 0;
    left: -100;
    margin-right: 100;
  }
  100% {
    opacity: 100%;
    left: 0;
    margin-right: 0;
  }
}

@keyframes new-todo {
  0% {
    opacity: 0;
    left: -100;
    margin-right: 100;
  }
  100% {
    opacity: 100%;
    left: 0;
    margin-right: 0;
  }
}
.hm-container {
  background-color: #ccc;
  margin: 5px;
  padding: 5px;
  border-radius: 5px;
}

.hm-controller {
  background-color: #aaa;
  margin: 5px;
  padding: 5px;
  border-radius: 5px;
}

.hm-displayer {
  background-color: #aaa;
  margin: 5px;
  padding: 5px;
  border-radius: 5px;
}

.hm-tooltip {
  background-color: rgba(32, 32, 32, 0.8);
  color: white;
  position: relative;
  top: -38px;
  min-width: 20px;
  border-radius: 5px;
  padding: 5px;
  display: inline-block;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  cursor: default;
}

.hm-tooltip-arrow {
  position: relative;
  width: 0px;
  height: 0px;
  top: -51px;
  border-left: 3px solid transparent;
  border-right: 3px solid transparent;
  border-top: 5px solid rgba(32, 32, 32, 0.8);
  display: inline-block;
}

.range-slider-handle {
  position: absolute;
  margin-left: -15px;
  margin-top: 25px;
  z-index: 2;
  width: 30px;
  height: 30px;
  border: 0px;
  text-align: center;
  cursor: pointer;
  border-radius: 50%;
  background-color: #2c4870;
  color: #333;
}

.range-slider-track {
  position: absolute;
  height: 10px;
  z-index: 1;
  margin-top: 35px;
  background-color: #546c91;
  border-radius: 5px;
  cursor: pointer;
}

.range-slider-rail {
  position: absolute;
  width: 100%;
  height: 10px;
  margin-top: 35px;
  border-radius: 5px;
  background-color: #8b9cb6;
  cursor: pointer;
}

.matchlist-item-dropdown {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  border-top-left-radius: 50px;
  border-bottom-left-radius: 50px;
  height: 50px;
  width: 170px;
}

.matchlist-item-close {
  border-top-right-radius: 50px;
  border-bottom-right-radius: 50px;
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
  height: 50px;
  width: 40px;
}

.matchlist-item-dropdown-menu {
  background-color: white;
  position: absolute;
  z-index: 500;
  left: 20%;
  width: 60%;
  height: 17rem;
  overflow-y: scroll;
  overflow-x: hidden;
}

.matchlist-item-match {
  position: relative;
  top: -7px;
  font-size: x-small;
  color: #777;
}

.matchlist-item-champ {
  position: relative;
  top: -10px;
  font-size: large;
}

.matchlist-add {
  border-radius: 50px;
  height: 50px;
  width: 50px;
  position: relative;
  overflow: hidden;
  vertical-align: middle;
  padding-top: 0px;
  cursor: pointer;
}

.matchlist-add-input {
  position: absolute;
  left: -100px;
  opacity: 0;
  height: 100px;
  cursor: pointer;
}
